import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class ImprovementSlide extends React.PureComponent {
  render() {
    return (
        <section className="slide">
            <h1>
                Improvement
            </h1>
            <p>
                Technology evolves rapidly. What has worked for many years no longer reflects today's realities and demand.
            </p>
            <p>
                We help transform, modernise and improve custom built software solutions. We believe in a better software every day.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_IMPROVEMENT}>Read more</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}