import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class ConsultancySlide extends React.PureComponent {
  render() {
    return (
        <section className="slide">
            <h1>
                Consultancy
            </h1>
            <p>
                Need a hand navigating through all these programming languages, platforms, frameworks, APIs and SDKs?
            </p>
            <p>
                We help keeping up with a pace of change. Let professional consultants help you.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_CONSULTANCY}>Read more</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}