import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class ContactSlide extends React.PureComponent {
  render() {
    return (
        <section className="slide">
            <h1>
                Contact us
            </h1>
            <p>
                We are here to help your business thrive.
            </p>
            <p>
                Talk to us about your needs and how we can help.
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}