import React from 'react';
import './JumbotronSlide.scss';

export class JumbotronSlideProps {
  backgroundImg?: string;
}

export class JumbotronSlideState {
  backgroundImg?: string;
}

export default class JumbotronSlide extends React.Component<JumbotronSlideProps, JumbotronSlideState> {
  render() {
    const rootStyle = {
      backgroundImage: 'url(' + this.props.backgroundImg + ')',
    };

    return (
      <div className="jumbotron-slide" style={rootStyle}>
        {this.props.children}
      </div>
    );
  }
}