import React from "react";
import { Jumbotron } from "../framework/Jumbotron";
import JumbotronSlide from "../framework/JumbotronSlide";
import EngineeringSlide from "../slides/EngineeringSlide";
import ImprovementSlide from "../slides/ImprovementSlide";
import ConsultancySlide from "../slides/ConsultancySlide";
import RecruitmentSlide from "../slides/RecruitmentSlide";
import ContactSlide from "../slides/ContactSlide";
import engineering from "../assets/jumbo-engineering.jpg"
import improvement from "../assets/jumbo-improvement.jpg"
import consultancy from "../assets/jumbo-consultancy.jpg"
import recruitment from "../assets/jumbo-recruitment.jpg"
import contact from "../assets/jumbo-contact.jpg"
import "../framework/site.scss";

export default class HomeContent extends React.Component {
    componentDidMount(): void {
        let images = [
            engineering,
            improvement,
            consultancy,
            recruitment,
            contact
        ];

        const links = document.getElementsByTagName("link");
        for (let i = 0, length = links.length; i < length; ++i) {
            const link = links.item(i);
            if (link) {
                images = images.filter(x => !link.href.endsWith(x));
            }
        }

        if (images) {
            images.forEach(x => {
                const link = document.createElement("link");
                link.href = x;
                link.rel = "preload";
                link.as = "image";

                document.head.appendChild(link);
            })
        }
    }

  render() {
    return (
      <main>
        <section className="jumbo">
          <Jumbotron>
              <JumbotronSlide backgroundImg={engineering}>
                  <EngineeringSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={improvement}>
                  <ImprovementSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={consultancy}>
                  <ConsultancySlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={recruitment}>
                  <RecruitmentSlide />
              </JumbotronSlide>
              <JumbotronSlide backgroundImg={contact}>
                  <ContactSlide />
              </JumbotronSlide>
          </Jumbotron>
        </section>
      </main>
    );
  }
}