import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class RecruitmentSlide extends React.PureComponent {
  render() {
    return (
        <section className="slide">
            <h1>
                Recruitment
            </h1>
            <p>
                IT professionals are in high demand. Finding right people is challenging.
            </p>
            <p>
                We listen to you. We know the people. We connect the dots.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_RECRUITMENT}>Read more</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
        </section>
    );
  }
}