import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class EngineeringSlide extends React.PureComponent {
  render() {
    return (
        <section className="slide">
            <h1>
                Engineering
            </h1>
            <p>
                We help governments, organisations and start-ups build custom software solutions for desktop, web, cloud and mobile.
            </p>
            <p>
                We were helping engineer software for decades, and we still do.
            </p>
            <p>
                <InternalLink routeID={RouteID.ROUTE_ID_ENGINEERING}>Read more</InternalLink>
            </p>
            <div>
                <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button" />
            </div>
      </section>
    );
  }
}