import React from 'react';
import JumbotronSlideProps from './JumbotronSlide';
import './Jumbotron.scss';

export interface JumbotronProps {
  children?: React.ReactElement<JumbotronSlideProps>[] | React.ReactElement<JumbotronSlideProps>
}

export interface JumbotronState {
  index: number
}

export class Jumbotron extends React.Component<JumbotronProps, JumbotronState> {
  private interval: any;

  constructor(props: JumbotronProps) {
    super(props);
    this.state = { index: 0 };
    this.switchTo = this.switchTo.bind(this);
  }

  componentDidMount() {
    this.setInterval();
  }

  componentWillUnmount() {
    this.clearInterval();
  }

  switchSlide() {
    if (this.props.children && this.state) {
      const count = React.Children.count(this.props.children);
      const index = this.state.index < count - 1 ? this.state.index + 1 : 0;
      this.setState({ index: index });
    }
  }

  switchTo(index: number) {
    this.clearInterval();
    this.setInterval();
    this.setState({ index: index });
  }

  setInterval() {
    this.interval = setInterval(() => this.switchSlide(), 5000);
  }

  clearInterval() {
    clearInterval(this.interval);
  }

  render() {
    const index = this.state ? this.state.index : 0;
    const child = React.Children.map(this.props.children, (child, i) => i === index && child);

    const switchers = React.Children.map(this.props.children, (child, i) => {
      if (i === index) {
        return <button className="jumbotron-switch selected" onClick={() => this.switchTo(i)} />
      } else {
        return <button className="jumbotron-switch" onClick={() => this.switchTo(i)} />
      }
    });

    return (
      <div className="jumbotron">
        {child}
        <div className="jumbotron-picker-position">
          <div className="jumbotron-picker-container">
            {switchers}
          </div>
        </div>
      </div>
    );
  }
}